
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import Drive from '@/tool/classFactory/drive.js';
export default {
components: {
Group,
Cell
},
data () {
return {
skills:[
{
name:'速成练习',
url:'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132'
},
{
name:'速成练习',
url:'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132'
},
{
name:'速成练习',
url:'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132'
}
],
params:{
'carType':'',
'type':'',
'sessionId':''
},
sessionId: '',
skillList:[]
}
},
created () {
this.params.carType = this.$route.query.carType || null
this.params.type = this.$route.query.type || null

this.params.sessionId = this.getCookie('sessionId')
console.log(this.sessionId)

this.getCategoryList()
},
methods: {
getCookie: function (objName) {
var arrStr = document.cookie.split("; ")
for (var i = 0; i < arrStr.length; i++) {
var temp = arrStr[i].split("=")
if(temp[0] == objName)
return unescape(temp[1])
}
},
getCategoryList () {
const that = this;
Drive.prototype.getCategoryList(that.params).then(res => {
let list = res.data.data || [];
that.skillList = list
console.log(list)
});
},

onExplainTap (item,index){
console.log(item)
console.log(index)
this.$router.push('../explain?carType=' + this.params.carType + '&&type=' + this.params.type +'&&catId=' + item.id + '&&mid=' + item.mid)

},
onTestTap (item,index){
this.$router.push('../skill?carType=' + this.params.carType + '&&type=' + this.params.type +'&&catId=' + item.id + '&&mid=' + item.mid)
}
}
}



